@import "src/common/styles/sass/variables";

.header{
    min-height: 60px;
    max-height: 120px;
    width: 100%;
    position: fixed;
    box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.3);
    z-index: 99;
    background-color: $backgroundColor;
}

.headerContainer{
    justify-content: space-between;
    align-items: center;
}
